import React, {Component} from 'react';
import HeaderComponent from "../../components/HeaderComponent/index";
import Info from "./subpage/Info";
import Comment from "./subpage/comment";
import Buy from "../../components/Buy/index";
import {connect} from 'react-redux';
import * as actions from '../../redux/actions/collection'

//,HomeDetail,this.props
/*
 * 1.history 
 * 2.match 
 * 3.location pathname
 * */

@connect(
    state => (
        {
            userInfo: state.userInfo,
            collectionList: state.collectionList
        }
    ),
    {...actions}
)
export default class Detail extends Component {


    //
    //
    //
    //

    //,
    constructor() {
        super();
        //IDredux,isCollectedtrue
        this.state = {
            isCollected: false
        }
    }


    //redux[]
    componentDidMount() {
        let id = this.props.match.params.id;
        let flag = this.props.collectionList.find((item) => {
            return item === id;
        });
        if (flag) {
            this.setState({
                isCollected: true
            })
        }
    }

    //
    checkIsLogin = () => {
        //true,false
        let username = this.props.userInfo.username;
        if (username) {
            return true;
        }
        return false;

    };

    collection = () => {
        //,,
        let flag = this.checkIsLogin();
        let id = this.props.match.params.id;
        if (flag) {
            if(this.state.isCollected){
                this.props.del(id);
            }else{
                this.props.add(id);
            }
            this.setState({
                isCollected: !this.state.isCollected
            })
        } else { //,,:route
            this.props.history.push(`/login/${encodeURIComponent(`/detail/${this.props.match.params.id}`)}`)
        }

    };

    buy = () => {
        //,,,
        let flag = this.checkIsLogin();
        if (flag) {
            this.props.history.push('/user');
        } else {
            //
            this.props.history.push(`/login/${encodeURIComponent(`/detail/${this.props.match.params.id}`)}`)
        }
    };

    render() {
        return (
            <div>
                {/**/}
                <HeaderComponent title={''} history={this.props.history}/>
                {/**/}
                <Info id={this.props.match.params.id}/>
                {/**/}
                <Buy isCollected={this.state.isCollected} collection={this.collection} buy={this.buy} />

                {/**/}
                <Comment id={this.props.match.params.id}/>
            </div>
        )
    }
}



