import React, {Component} from 'react';
import './index.less';
export default class LoginComponent extends Component {

    constructor() {
        super();
        this.state = {  //input
            val: ''
        }
    }

    changeValue = (e) => {
        /*
         * ref
         * state,,onChange   onChange+state
         * 
         * */
        let val = e.target.value;
        this.setState({   //state
            val
        })

    };

    login = () => {
        let username = this.state.val;
        this.props.login(username);

    };

    render() {
        return (
            <div className="login-component">
                <input ref='username' onChange={this.changeValue} placeholder="" value={this.state.val}
                       type="text"/>
                <button className="back" onClick={this.login}></button>
            </div>
        )
    }
}



