import 'whatwg-fetch';
import 'es6-promise';


export function get(url) {
    return fetch(url, {
        headers: {
            accept: 'application/json'
        }
    }).then(res => res.json())
}


function toUrlencoded(obj) {
    let ary = [];
    for (let key in obj) {
        ary.push(`${key} = ${obj[key]}`);
    }
    return ary.join('&');
}

export function post(url, obj) {  //id:1,content:ok ===>     a=1&b=2&c=3
    return fetch(url, {
        method: 'POST',
        headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
        },
        body: toUrlencoded(obj)
    }).then(res=>res.json())     /*JSON,json,*/
}


