import React, {Component} from 'react';
import HeaderComponent from "../../components/HeaderComponent/index";
import LoginComponent from '../../components/LoginComponent/index';
import {connect} from 'react-redux';
import * as actions from '../../redux/actions/userInfo';

/*
 * 1.storeuserInfo
 * 2.actions
 * */
@connect(
    state => (
        {userInfo: state.userInfo}   /*keyreducer*/
    ),
    {...actions}
)
export default class Login extends Component {

    //
    constructor() {
        super();
        this.state = {Login: false}
        /* ...*/
        /**/
    }

    componentDidMount() {
        this.checkIsLogin();  //storeusername
    }

    //,,state.Login,
    checkIsLogin = () => {
        //reduxusername,
        if (this.props.userInfo.username) {//,
            this.props.history.push('/user');
        }
        //,,
        this.setState({
            Login: true   //
        })
    };

    //,redux,UI
    login = (username) => {
        //store
        let info = this.props.userInfo;
        //username
        info.username = username;
        //store
        this.props.update(info);
        //
        //
        //,
        if (this.props.match.params.route) {
            //;
            this.props.history.push(decodeURIComponent(this.props.match.params.route));
        } else {
            this.props.history.push('/user');
        }
    };

    render() {
        return (
            <div>
                <HeaderComponent title={''} history={this.props.history}/>
                {/*,*/}
                {this.state.Login ? <LoginComponent login={this.login}/> : ''}
            </div>
        )
    }
}



